body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.download-link{
  background-color: blue;
  color: white;
  border-radius: 30px;
  font-size: 22px;
}
.counter{
  background-color: #FFFBEF;
  padding: 40px 0;
}
.counter .conter-area h3{
  color: #7918CF;
  font-size: 44px;
  font-weight:800;
}
.counter .conter-area p{
  color:black;
  font-weight: 600;
  font-size: 18px;
}
.hussel{
  padding: 40px 0;
  text-align: left;
}
.hussel h2{
  font-size: 44px;
  color: #7918CF;
  font-weight: 800;
}
.read-btn{
  margin-top: 20px;
}
.read-btn .read{
  background: linear-gradient(90deg, #9b12e1, #ce1dde);
  color: white;
  border-radius: 50px;
  padding: 8px 22px;
  text-decoration: none;
}
.view{
  margin: 20px 0; 
}
.viewall{
  margin: 20px 0;
  background: linear-gradient(90deg, #9b12e1, #ce1dde);
  color: white;
  border-radius: 50px;
  padding: 8px 22px;
  text-decoration: none; 
}
.features h3{
  text-align: left;
  padding: 30px 0;
}
.features .card .card-body{
  text-align: left;
}
.features .card{
  background-color: white;
  border-radius: 20px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.features .card:hover{
  background-color: #7918CF;
  color: white;
}
.footer-logo{
  text-align: left;
}
.footer{
  padding: 20px 0;
}
.footer-ul ul li{
  padding: 10px;
  display: inline-block;
  font-weight: 500;
}
.nav-item{
  padding: 5px 20px;
}
.nav-item a{
  font-weight: 700;
}
.download{
  background: linear-gradient(90deg, #9b12e1, #ce1dde);
  color: white;
  border-radius: 50px;
  border: none;
  padding: 8px 22px;
  text-decoration: none;
  font-weight: 700;
  font-size: 19px;
}
.frequently h5{
  text-align: left;
}
.frequently ol li{
  text-align: left;
}
.footer p{
  text-align: left;
}
.footer-content{
  display: flex;
  text-align: right;
  float: right;
}

.footer-content ul li{
  list-style-type: none;
  display: inline-block;
  padding: 0px 10px;
}
.story{
  background-color: #efefef;
  padding: 30px 0;
}
.page-bread{
  background-image: url(../src/assets/bread.jpg);
  padding:30px;
}